.workspage {
    padding: 0 40px;

    .masonry {
        margin-top: 40px;
        columns: 4;
        column-gap: 16px;

        @media (max-width: 1200px) {
            columns: 3;
        }

        @media (max-width: 992px) {
            columns: 2;
        }

        @media (max-width: 600px) {
            columns: 1;
        }

        .grid {
            display: inline-block;
            margin-bottom: 16px;
            position: relative;

            &:before {
                border-radius: 5px;
                content: '';
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                height: 100%;
                background-color: rgba(0, 0, 0, .6);
            }

            &:hover {
                &::before {
                    background-color: rgba(80, 80, 80, 0.5);
                }
            }

            img {
                width: 100%;
                border-radius: 5px;
            }

            &__title {
                font-size: 28px;
                font-weight: bold;
                margin: 0px 0px 10px 0px;
            }

            &__author {
                font-size: 14px;
                font-weight: 300;
            }

            &__link {
                position: absolute;
                left: 0;
                right: 0;
                top: 0;
                bottom: 0;
            }

            &__body {
                position: absolute;
                left: 0;
                right: 0;
                top: 0;
                bottom: 0;
                padding: 10px 20px 20px 20px;
                color: #fff;
                display: flex;
                flex-direction: column
            }

            &__tag {
                background-color: rgba(255, 255, 255, .8);
                color: #333;
                border-radius: 5px;
                padding: 5px 15px;
                margin-bottom: 5px;
            }
        }
    }

    .mt-auto {
        margin-top: auto;
    }

}